<template>
  <div class="fav">
    <div class="container border-top">
      <h4>My Wishlist 我的收藏</h4>
      <div
        v-if="FavoriteData < 1"
        class="text-center border-top p-3"
        style="font-size: 13px"
      >
        您還沒有任何收藏
      </div>
      <div
        class="row border-bottom mb-3"
        v-for="(favorite, index) in FavoriteData"
        :key="favorite.id"
      >
        <div class="col-sm-3 mb-3">
          <router-link
            :to="{ name: 'product-detail', params: { id: favorite.id } }"
          >
            <img class="img" :src="favorite.image" alt="" />
          </router-link>
        </div>
        <div class="col-sm-3 fav-detail">
          <router-link
            :to="{ name: 'product-detail', params: { id: favorite.id } }"
          >
            <div class="item-name">{{ favorite.title }}</div>
          </router-link>
        </div>
        <div class="col-sm-3 fav-detail">
          <span class="item-price">NT$ {{ favorite.price }}</span>
        </div>
        <div class="col-sm-3 mt-2 mb-3">
          <button
            type="button"
            class="btn btn-outline-secondary"
            @click.prevent="
              addShoppingCart(
                favorite.title,
                favorite.image,
                favorite.price,
                favorite.id
              )
            "
          >
            Add to cart
          </button>
          <font-awesome-icon
            :icon="['far', 'trash-alt']"
            size="lg"
            class="icon me-4 ms-4"
            @click.prevent="removeFromFavorite(index)"
          />
        </div>
      </div>
    </div>
    <router-link to="/new" class="row">
      <button type="button" class="btn btn-light">
        Continue shopping
      </button>
    </router-link>
  </div>
</template>

<script>
import { Toast } from "./../utils/helpers";

export default {
  name: "FavoriteList",
  methods: {
    addShoppingCart: function(title, image, price, id) {
      this.$store.commit("setShoppingCart", {
        id: id,
        title: title,
        image: image,
        price: price,
        count: 1,
      });
      Toast.fire({
        icon: "success",
        title: "已加入購物車",
      });
    },
    removeFromFavorite: function(index) {
      this.$store.commit("deleteFavoriteItem", index);
    },
  },
  computed: {
    FavoriteData: function() {
      return this.$store.getters.getFavoriteItem;
    },
  },
};
</script>

